import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { CartService } from '../cart.service';
import { FlashMessagesService } from 'angular2-flash-messages';

@Component({
  selector: 'app-cart',
  templateUrl: './cart.component.html',
  styleUrls: ['./cart.component.css']
})
export class CartComponent implements OnInit {

  constructor(
    private cart: CartService,
    private flash : FlashMessagesService,
    private router: Router    
    ) { }

  items:any;
  total:number=0;
  n:number=0;
  p:number = 1;

  ngOnInit(): void {
    this.items = this.cart.getAllItems();
    this.total = this.cart.totalPrice();
    this.cart.numberOfItems().subscribe((items:number) => this.n = items);

  }
  addqty(item:any)
  {
    item.qty++;  
    this.cart.changeQty(item);
    this.items = this.cart.getAllItems();
    this.total = this.cart.totalPrice();

  }
  decqty(item:any)
  {
    if(item.qty<=1){
      this.flash.show('Minimum quantity must be one !!', { cssClass: 'alert-danger', timeout: 5000 });
      
    }
    else{
      item.qty--;  
      this.cart.changeQty(item);
      this.items = this.cart.getAllItems();
      this.total = this.cart.totalPrice();
    }
  }
  onRemoveClick(item:any)
  {
    this.cart.deleteItem(item);
    this.items = this.cart.getAllItems();
    this.total = this.cart.totalPrice();
    this.flash.show('Sucessfully removed item from cart!', { cssClass: 'alert-success', timeout: 5000 });
      
  }
  onCheckoutLinkClick()
  {
    this.router.navigateByUrl('/checkout');
  }

}
