import { Component, OnInit } from '@angular/core';
import { AuthService } from '../../auth/services/auth.service';
import { Router } from '@angular/router';
import { LoadingService } from '../../loading.service';
import { FlashMessagesService } from 'angular2-flash-messages';

@Component({
  selector: 'app-settings',
  templateUrl: './settings.component.html',
  styleUrls: ['./settings.component.css']
})
export class SettingsComponent implements OnInit {
  user:any;
  address =  {
    line1 :"", 
    line2 :"",
    city:"",
    state:"",
    pincode:"",
    type:""
  }

  constructor(
    private authService: AuthService,
    private router: Router,    
    private load : LoadingService,
    private flash: FlashMessagesService
    ) { }

  ngOnInit(): void {
    this.load.changeloadStatus(false);
    this.authService.getUserDetails().subscribe((profile:any) => {
      if(profile){        
        this.load.changeloadStatus(true);
        this.user = profile.user;
        if(this.user.address.length<1){
          this.user.address.push(this.address);
        }
      } 
    },
    (err) => {
      console.log(err);
      return false;
    });
  }

  onAddAddress(){
    this.user.address.push(this.address);
  }

  onUpdateSubmit(){
    this.authService.editUserDetails(this.user).subscribe((data:any) => {
      if(data.success){      
        this.authService.updateUserData(data.user);
        this.flash.show(data.msg, { cssClass: 'alert-success', timeout: 10000 });
      }
      else{
        this.flash.show(data.msg, { cssClass: 'alert-danger', timeout: 10000 });
      }
    });
  }

}
