import { Component, OnInit } from '@angular/core';
import { OrderService } from '../order.service';
import { ProductService } from 'src/app/product.service';
import { AuthService } from '../../auth/services/auth.service';
import { LoadingService } from '../../loading.service';
import { FlashMessagesService } from 'angular2-flash-messages';

@Component({
  selector: 'app-orders',
  templateUrl: './orders.component.html',
  styleUrls: ['./orders.component.css']
})
export class OrdersComponent implements OnInit {

  constructor(
    private order : OrderService,
    private authService : AuthService,
    private ps : ProductService,
    private load : LoadingService,   
    private flash : FlashMessagesService
  ) { }
  
  orders:any = [];
  user_id:any;
  p:number = 1;
  products: any = [];
  
 ngOnInit(): void {   
    this.load.changeloadStatus(false);
    this.order.getAllOrders().subscribe((orders:any) => {
      if(orders){          
        this.load.changeloadStatus(true);
        this.orders = orders;
      }
    });
    this.ps.get().subscribe((products:any) => {this.products = products});
  }
  onDeleteClick(id:any){
    this.order.cancelOrder({delete_id : id}).subscribe((data:any) => {
      if(data.success){
        this.flash.show(data.msg, { cssClass: 'alert-success', timeout: 10000 });
        this.load.changeloadStatus(false);
        this.order.getAllOrders().subscribe((orders:any) => {
          if(orders){          
            this.load.changeloadStatus(true);
            this.orders = orders;
          }
        });
        this.ps.get().subscribe((products:any) => {this.products = products});
      }
    });
  }


}
