import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';


@Injectable({
  providedIn: 'root'
})
export class CartService {

  constructor() { }
  
  item:any;
  private numberofitems = new BehaviorSubject(0);

  addToCart(product:any)
  {
    this.item = {
      product: product,
      qty: 1
    }
    
    if(localStorage.getItem('cart')  == null){
      localStorage.setItem('cart', JSON.stringify([this.item]));
    }
    else
    {
      let itemsInCart = JSON.parse(localStorage.getItem('cart')  || '{}');
      for(var item of itemsInCart)
      {
        if(this.item.product._id == item.product._id)
        {
          item.qty += 1;
          this.item=null;
          break;  
        }
      } 
      if(this.item)
      {
        itemsInCart.push(this.item);
      }
      localStorage.setItem('cart', JSON.stringify(itemsInCart));      
      this.numberofitems.next(itemsInCart.length);
     }
  }
  getAllItems(){
    return JSON.parse(localStorage.getItem('cart')|| '{}');  
   }
  numberOfItems(): Observable<number>{
    let itemsInCart = JSON.parse(localStorage.getItem('cart')|| '{}');
    this.numberofitems.next(itemsInCart.length);    
    return this.numberofitems;
  }
  deleteItem(item:any){
    this.item = item;
    let itemsInCart = JSON.parse(localStorage.getItem('cart')|| '{}');
    for(let i in itemsInCart){
      if (this.item.product._id == itemsInCart[i].product._id)
      {
        itemsInCart.splice(i, 1);
        break;
      }
    }
    localStorage.setItem('cart', JSON.stringify(itemsInCart));
    this.numberofitems.next(itemsInCart.length);
  }
  changeQty(item:any)
  {
    this.item = item;
    let itemsInCart = JSON.parse(localStorage.getItem('cart') || '{}');
    for(let item of itemsInCart){
      if(this.item.product._id == item.product._id){
        item.qty = this.item.qty;
        item = null;
        break;
      }
    }
    localStorage.setItem('cart', JSON.stringify(itemsInCart));
  }
  totalPrice(){
    let total=0;
    let itemsInCart = JSON.parse(localStorage.getItem('cart') || '{}');
    for(let item of itemsInCart){
      total += item.product.price*item.qty;
    }
    return total;
  }
  clearCart(){
    localStorage.removeItem('cart');
  }
}
