import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from  '@angular/common/http';
import { BehaviorSubject, Observable } from 'rxjs';
import { map } from 'rxjs/operators/';
@Injectable({
  providedIn: 'root'
})
export class AuthService {
   authToken: any;
   user:any;
   private name = new BehaviorSubject("");

  constructor(private http: HttpClient) { }

  registerUser(user:any){
    return this.http.post("http://localhost:2000/register",user);
  }

  authenticateUser(user:any){
    return this.http.post("http://localhost:2000/authenticate",user);
  }
  getUserDetails(){
    this.loadToken();
    let headers = new HttpHeaders().set('Authorization', this.authToken);
    return this.http.get("http://localhost:2000/user",{headers: headers});
  }

  getUsersName() : Observable<string>{
    const user = JSON.parse(localStorage.getItem('user') || "{}");
    this.name.next(user.name);
    return this.name;
  }

  editUserDetails(user:any){
    this.loadToken();
    let headers = new HttpHeaders().set('Authorization', this.authToken);
    return this.http.post("http://localhost:2000/update",user,{headers: headers});
  }

  storeUserData(token :any, user :any){
    localStorage.setItem('id_token', token);
    localStorage.setItem('user', JSON.stringify(user));
    this.authToken = token;
    this.user = user;
    this.name.next(user.name);
  }

  updateUserData(user :any){
    localStorage.setItem('user', JSON.stringify(user));
    this.user = user;
    this.name.next(user.name);
  }

  loadToken(){
    const token = localStorage.getItem('id_token');
    this.authToken = token;
  }

  loggedIn() {
    this.loadToken();
    return (this.authToken !== null) ? true : false;
  }

  logoutUser(){
    this.authToken= null;
    this.user = null;
    localStorage.clear();
  }

}
