import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class LoadingService {

private loaded = new BehaviorSubject(true);
loadStatus = this.loaded.asObservable();

constructor() { }

changeloadStatus(status: boolean) {
  this.loaded.next(status);
}
}
