import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router'
import { ValidateService } from '../services/validate.service';
import { AuthService } from '../services/auth.service';
import { FlashMessagesService } from 'angular2-flash-messages';



@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css','../auth_style.css']
})
export class RegisterComponent implements OnInit {

  constructor(
    private router: Router,
    private validate: ValidateService, 
    private auth : AuthService,
    private flash : FlashMessagesService
    ) { }

  
  message: any;
  
  ngOnInit(): void {
  }

  onEmailEnter(event: any){
    //Email Validity Check
    if(!this.validate.ValidateEmail(event.target.value)){
      this.message = 'Please enter a valid email.';
      setTimeout (() => {
        this.message = null;
      }, 7000);
    }
  }
  onPasswordEnter(event: any){
    //Password Validity Check
    if(!this.validate.ValidatePassword(event.target.value)){
      this.message = 'Password must contain min 8 characters, one digit, one symbol, one uppercase letter.';
    }
    setTimeout (() => {
      this.message = null;
    }, 7000);
  }
 
  onRegistrationSubmit(form:any) : any{
    var user = {
      name: form.name,
      email: form.email,
      password: form.password
    }
    //Terms Agreed Check
    if(!form.agree){
      this.flash.show('You must Agree to Terms & Conditions', { cssClass: 'alert-danger', timeout: 5000 });
      return false;
    }
    //Required Field Check
    if(!this.validate.validateRegister(user)){
      this.flash.show('Please fill up all the fields', { cssClass: 'alert-danger', timeout: 5000 });
      return false;
    }
    //Email Validity Check
    if(!this.validate.ValidateEmail(user.email)){
      this.flash.show('Please enter a valid email.', { cssClass: 'alert-danger', timeout: 5000 });
      return false;
    }
    //Password Validity Check
    if(!this.validate.ValidatePassword(user.password)){
      this.flash.show('Password must contain min 8 characters, one digit, one symbol, one uppercase letter.', { cssClass: 'alert-danger', timeout: 5000 });
      return false;
    }
    //Password Match Check
    if(user.password!=form.repassword){
      this.flash.show('Passwords don\'t match.', { cssClass: 'alert-danger', timeout: 5000 });
      return false;
    }
    this.auth.registerUser(user).subscribe((data:any) => {
      if(data.success){
        this.flash.show(data.msg, { cssClass: 'alert-success', timeout: 10000 });
        this.router.navigateByUrl('/login');
      }
      else{
        this.flash.show(data.msg, { cssClass: 'alert-danger', timeout: 10000 });
        this.router.navigateByUrl('/register');
      }
    })

  }
}
