import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ProductService } from '../product.service';
import { Ng2SearchPipeModule } from 'ng2-search-filter';
import { NgxPaginationModule } from 'ngx-pagination';
import { SearchService } from '../search.service';
import { CartService } from '../cart.service';
import { LoadingService } from '../loading.service';
import { FlashMessagesService } from 'angular2-flash-messages';

@Component({
  selector: 'app-product-list',
  templateUrl: './product-list.component.html',
  styleUrls: ['./product-list.component.css']
})
export class ProductListComponent implements OnInit {

  constructor(private ps: ProductService, 
    private search: SearchService,    
    private route : ActivatedRoute,
    private cart : CartService,
    private load : LoadingService,    
    private flash : FlashMessagesService ) { }
  
  p:number = 1;
  keyword:string ="";
  products:any=[];
  sort:string="name";

  ngOnInit(): void {     
    this.load.changeloadStatus(false);  
    this.search.keywordTyped.subscribe(keyword => this.keyword = keyword);
    this.route.queryParams.subscribe(params => this.keyword = params.category);
    this.ps.get().subscribe(
      (data:any) => {
        if(data){
          this.load.changeloadStatus(true);
          this.products=data;
        }
      });    
  }
  onAddToCartClick(product : any){
    this.cart.addToCart(product);
    this.flash.show('Item added to cart successfully.', { cssClass: 'alert-success', timeout: 5000 });  
  }
}

