import { Component, OnInit } from '@angular/core';
import { ProductService } from '../product.service';
import { LoadingService } from '../loading.service';
import { ActivatedRoute } from '@angular/router';
import { CartService } from '../cart.service';
import { FlashMessagesService } from 'angular2-flash-messages';
@Component({
  selector: 'app-product-detail',
  templateUrl: './product-detail.component.html',
  styleUrls: ['./product-detail.component.css']
})
export class ProductDetailComponent implements OnInit {

  constructor(
    private ps: ProductService,
    private load : LoadingService,
    private cart : CartService,
    private flash : FlashMessagesService,       
    private route : ActivatedRoute
  ) { }

  id:any;
  product:any;

  ngOnInit(): void {    
    this.load.changeloadStatus(false);
    this.route.queryParams.subscribe(params => this.id = params.id);
    this.ps.getProduct(this.id).subscribe((product:any) => {
      if(product){          
        this.load.changeloadStatus(true);
        console.log(product);
        this.product = product;
      }
    });
  }
  onAddToCartClick(product : any){
    this.cart.addToCart(product);
    this.flash.show('Item added to cart successfully.', { cssClass: 'alert-success', timeout: 5000 });  
  }

}
