import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { CartService } from '../../cart.service';
import { OrderService } from '../order.service';
import { AuthService } from '../../auth/services/auth.service'
import { FlashMessagesService } from 'angular2-flash-messages';

@Component({
  selector: 'app-checkout',
  templateUrl: './checkout.component.html',
  styleUrls: ['./checkout.component.css']
})
export class CheckoutComponent implements OnInit {

  constructor(
    private cart: CartService,
    private order: OrderService,
    private router: Router,    
    private authService: AuthService,    
    private flash : FlashMessagesService
  ) { }
    
  neworder:{
    productlist: {_id: string}[],
    paymentmethod: string,
    amount:number,
    address:string,
    user_id:String

  } = {
    productlist: [],
    paymentmethod:"",
    amount:0,
    address:"",
    user_id:""
  }

  items:any;
  p:number = 1;
  address:any = [];


  ngOnInit(): void {
    this.items = this.cart.getAllItems();
    this.neworder.amount = this.cart.totalPrice();
    this.authService.getUserDetails().subscribe((profile:any) => {
      this.neworder.user_id = profile.user._id;
      for (let i in profile.user.address){
        console.log(profile.user.address[i]);
        this.address.push(Object.values(profile.user.address[i]));
      };
    });
    for(let item of this.items){
      this.neworder.productlist.push(item.product._id);
    }
  }

  onCheckoutClick(){          
    this.order.Order(this.neworder).subscribe((data:any) => {
      if(data.success){
        this.flash.show(data.msg, { cssClass: 'alert-success', timeout: 10000 });
        this.cart.clearCart();
        this.router.navigateByUrl('orders');        
      }
      else{
        this.flash.show(data.msg, { cssClass: 'alert-danger', timeout: 10000 });
      }
    });
  }

}
