import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from  '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class OrderService {

  constructor(private http: HttpClient) { }

  authToken:any;
  Order(order:any){
    this.loadToken();
    let headers = new HttpHeaders().set('Authorization', this.authToken);
    return this.http.post("/order/order",order, {headers: headers});
  }
  getAllOrders(){
    this.loadToken();
    let headers = new HttpHeaders().set('Authorization', this.authToken);
    return this.http.get("/order/get",{headers: headers});
  }
  cancelOrder(id:any){
    this.loadToken();
    let headers = new HttpHeaders().set('Authorization', this.authToken);
    return this.http.post("/order/cancel",id, {headers: headers});
  }
  loadToken(){
    const token = localStorage.getItem('id_token');
    this.authToken = token;
  }
}
