import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router'
import { AuthService } from '../services/auth.service';
import { FlashMessagesService } from 'angular2-flash-messages';


@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css','../auth_style.css']
})
export class LoginComponent implements OnInit {

  constructor(
    private router: Router,
    private auth : AuthService,
    private flash : FlashMessagesService
  ) { }

  ngOnInit(): void {
  }
  onLoginSubmit(form:any){
    var user = {
      email: form.email,
      password: form.password
    }
    this.auth.authenticateUser(user).subscribe((data:any) => {
      if(data.success){
        this.auth.storeUserData(data.token, data.user);
        this.flash.show(data.msg, { cssClass: 'alert-success', timeout: 10000 });
        this.router.navigateByUrl('/');
      }
      else{
        this.flash.show(data.msg, { cssClass: 'alert-danger', timeout: 10000 });
        this.router.navigateByUrl('/login');
      }
    })
  }
}
