import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router'
import { AuthService } from '../auth/services/auth.service';
import { FlashMessagesService } from 'angular2-flash-messages';
import { FormsModule } from '@angular/forms';
import { SearchService } from '../search.service';
import { CartService } from '../cart.service';

@Component({
  selector: 'app-navbar',
  templateUrl: './navbar.component.html',
  styleUrls: ['./navbar.component.css']
})
export class NavbarComponent implements OnInit {

  constructor(
    private router: Router,
    private auth : AuthService,
    private flash : FlashMessagesService,
    private search: SearchService,
    private cart: CartService 
  ) { }

  keyword : any;
  name : string = "";
  items : number = 0;

  ngOnInit(): void {
      this.auth.getUsersName().subscribe((name:string) => {
        this.name = name;
      },
      (err)=>{
          console.log(err);
          return false;
      });
    this.cart.numberOfItems().subscribe((items:number) => this.items = items);
  }
  onLogoutClick(){
    this.auth.logoutUser();
    this.flash.show('Successfuly Signed out', { cssClass: 'alert-success', timeout: 10000 });
    this.router.navigateByUrl('/');
    return false;
  }
  onSearchClick(){
    this.search.changeSearchTerm(this.keyword);    
    this.router.navigateByUrl('/products');
  }
  onSearchType(){
    this.search.changeSearchTerm(this.keyword);
  }
  isLoggedIn(){
    return this.auth.loggedIn();

  }

}
